#editor-container {
    margin: 0 auto;
    margin-top: 2em;
    width: 60%;
    height: 15em;
    background-color: red;
    min-height: 300px;
    max-width: 690px;
}

#editor {
    width: 100%;
    height: 100%;
    padding: 1em;
}

#editor:focus-visible {
    outline: none;
}

